#wiimdApp {
  background: linear-gradient(0deg, #010c12 0%, #033536 50%, #28434a 100%);
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

#wiimdApp .wiimdContainer {
  color: #fff;
  grid-template: "wiimdAlbumArt" 1.4fr
                 "wiimdMediaInfo" .6fr
                 "wiimdFooter" 120px
                 / 1fr;
  grid-auto-flow: row;
  gap: 0;
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wiimdApp .wiimdAlbumArt {
  text-align: center;
  grid-area: wiimdAlbumArt;
  padding: 40px 24px 16px;
  overflow: hidden;
}

#wiimdApp .wiimdMediaInfo {
  grid-area: wiimdMediaInfo;
  padding: 0 24px;
}

#wiimdApp .wiimdFooter {
  grid-area: wiimdFooter;
  padding: 0 24px;
}

#wiimdApp .text-secondary {
  color: #b3bdba !important;
}

#wiimdApp #wiimdBgAlbumArt {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wiimdApp #wiimdBgAlbumArt #bgAlbumArtBlur {
  filter: blur(12px) brightness(.6) opacity(.6);
  background-image: url("fake-album-1.69685f7b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  transform: scale(1.25);
}

#wiimdApp .wiimdAlbumArt > div {
  justify-content: center;
  align-items: center;
  max-width: 100%;
  max-height: 100%;
  display: flex;
  overflow: hidden;
}

#wiimdApp .wiimdAlbumArt #albumArt {
  width: 80%;
  box-shadow: 3px 3px 16px #000000bf;
}

#wiimdApp .wiimdMediaInfo {
  text-shadow: 3px 3px 8px #000000bf;
}

#wiimdApp .wiimdMediaInfo #mediaSource {
  text-align: right;
}

#wiimdApp .wiimdMediaInfo #mediaSource img {
  filter: invert();
  max-height: 24px;
  margin-bottom: 15px;
}

#wiimdApp .wiimdMediaInfo #placeholder {
  margin-top: 10%;
}

#wiimdApp .wiimdMediaInfo h1 {
  font-size: calc(1.375rem + 1.7vw);
}

#wiimdApp .wiimdMediaInfo h2 {
  font-size: calc(1.325rem + 2.2vw);
}

#wiimdApp .wiimdMediaInfo h3 {
  font-size: calc(1.3rem + 1.5vw);
}

#wiimdApp .wiimdMediaInfo h4 {
  font-size: calc(1.275rem + .8vw);
}

#wiimdApp .wiimdMediaInfo div, #wiimdApp .wiimdMediaInfo p {
  font-size: calc(.875rem + .8vw);
}

#wiimdApp .wiimdMediaInfo div:has( > #mediaAlbum), #wiimdApp .wiimdMediaInfo #mediaQuality, #wiimdApp .wiimdMediaInfo #mediaSubTitle {
  font-size: calc(.875rem + .3vw);
}

#wiimdApp .wiimdMediaInfo #mediaAlbumIdent.badge-outlined, #wiimdApp .wiimdMediaInfo #mediaQualityIdent.badge-outlined {
  font-weight: var(--bs-body-font-weight);
  border: 1px solid #767d7b;
  margin-right: 10px;
}

#wiimdApp .wiimdFooter #progressPercent {
  --bs-bg-opacity: .5;
  height: 2px;
}

#wiimdApp .wiimdFooter #devNameHolder {
  font-size: calc(.875rem + .3vw);
  position: absolute;
  bottom: 30px;
  left: 24px;
}

#wiimdApp .wiimdFooter #devVolumeHolder {
  text-align: right;
  font-size: calc(.875rem + .3vw);
  position: absolute;
  bottom: 30px;
  right: 24px;
}

@media (width >= 576px) and (orientation: landscape) {
  #wiimdApp .wiimdContainer {
    grid-template: "wiimdAlbumArt wiimdMediaInfo" 1fr
                   "wiimdFooter wiimdFooter" 105px
                   / .65fr 1.35fr;
  }

  #wiimdApp .wiimdAlbumArt {
    padding: 24px 24px 16px;
  }

  #wiimdApp .wiimdMediaInfo {
    padding: 24px 24px 24px 0;
  }

  #wiimdApp .wiimdFooter {
    padding: 0 24px;
  }

  #wiimdApp .wiimdFooter .btn-group.primary {
    margin-top: 5px;
  }
}

@media (width >= 768px) and (orientation: landscape) {
  #wiimdApp .wiimdContainer {
    grid-template-rows: 1fr 105px;
    grid-template-columns: .95fr 1.05fr;
  }

  #wiimdApp .wiimdMediaInfo #mediaSource img {
    max-height: 28px;
  }

  #wiimdApp .wiimdFooter .btn-group.primary {
    margin-top: 2px;
  }
}

@media (width >= 992px) and (orientation: landscape) {
  #wiimdApp .wiimdMediaInfo #mediaSource img {
    max-height: 30px;
  }
}

@media (width >= 1200px) and (orientation: landscape) {
  #wiimdApp .wiimdMediaInfo #mediaSource img {
    max-height: 32px;
  }
}
